:root {
  --primaryColor: #90a959;
  --setcondColor: #E9B872;
  --mainWhite: #fff;
  --mainBlack: #222;
  --mainRed: #c45151;
  --mainGreen: #4caf50;
}
* {
  margin: 0;
  box-sizing: border-box;
}

body{
  background-color: #E9B872;
  background-color: var(--setcondColor);
  color: #222;
  color: var(--mainBlack);
}

h1 {
  margin: 1rem 0;
}
.form-center{
    display: flex;
}

.form-group{
    padding: 1rem 0.75rem;
    flex: 1 1;
}

label{
    display: block;
    color: var(--setcondColor);
    font-size: 1rem;
    text-transform: capitalize;
}

.form-control{
    background-color: transparent;
    border: none;
    border-bottom: 1px solid var(--setcondColor);
    outline: none;
    height: 3rem;
    width: 100%;
    font-size: 16px;
    margin: 0 0 8px 0;
    padding: 0;
}
.main-container{
    margin: 0 auto;
    max-width: 1280px;
    width: 90%;
}
.item {
    background-color: var(--mainWhite);
    line-height: 1.5rem;
    padding: 10px 20px;
    margin-bottom: 1rem;
    border: 1px solid #e0e0e0;
    display: flex;
    justify-content: space-between;
    transition: all 0.3s linear;
}

.tiem:hover {
    width: 100%;
    -webkit-transform: scale(1.02);
            transform: scale(1.02);
}

.info{
    flex:0 0 60%;
    display: flex;
    justify-content: space-between;
}

.expense{
    margin-right: 2rem;
}

.amount{
    font-weight: 300;
    font-size: 1rem;
    border-radius: 2px;
    padding: 1px 3px;
}

.clear-btn,
.edit-btn{
    background-color: none;
    border: none;
    font-size: 1.2rem;
    outline: none;
    cursor: pointer;
}

.clear-btn{
    color: var(--mainRed);
}

.edit-btn{
    color: var(--mainGreen);
}
.list {
    list-style-type: none;
    border: none;
    padding: 0;
}

